@import "variables.scss";
@import "mixins.scss";

body {
  overflow: hidden; /* Hide scrollbars */
}

.view__container {
  position: absolute;
  height: 1000vh;
  width: 1000vw;
  transition: transform 2s ease-in-out;
  background: url("/assets/img/textureContainer.jpg");
}

.view {
  position: absolute;
  width: 110vw;
  height: 110vh;

  .element {
    position: absolute;
    transition: transform 0.5s ease-in-out;
  }

  .paper {
    // Chapter selection styling
    background: no-repeat url("/assets/img/home/Paper.png");
    background-size: cover;
    display: flex;
    font-family: $base-font-family-title, cursive;
    color: #f33a3a;
    font-size: 35px;
    font-weight: 600;
    justify-content: center;
    align-items: center;
    width: 380px;
    height: 135px;
  }

  .text {
    background: no-repeat url("/assets/img/texturePaper.png");
    background-size: cover;
    font-family: $base-font-family-text;
    font-size: $base-font-size;
    line-height: $base-line-height;
    box-shadow: 4px 4px 12px rgba($color: black, $alpha: 0.4);
  }
}

.title {
  position: relative;
  font-family: $base-font-family-title;
  font-size: 55px;
  color: $base-color-title;
  text-align: center;
}

.scotch {
  background: no-repeat center url("/assets/img/Vector.png");
  background-size: contain;
  width: 150px;
  height: 50px;
  position: absolute;
}

.shadow {
  box-shadow: 2px 2px 16px rgba($color: black, $alpha: 0.3);
}

.menu {
  font-family: $base-font-family-title;
  font-size: 55px;
  color: $base-color-title;
  text-align: center;
}

.menu:hover {
  cursor: pointer;
}

.audioControl {
  background: no-repeat center url("/assets/speaker.png");
  background-size: contain;
  width: 50px;
  height: 50px;
  position: fixed;
  top: 50px;
  right: 200px;
}

.audioControl:hover {
  cursor: pointer;
}

.sound {
  position: absolute;
  top: -15px;
  width: 6px;
  height: 60px;
  background: black;
  border-radius: 5%;
  transform-origin: bottom left;
  transform: rotate(45deg) scaleY(0);
  transition: all 0.3s ease-in-out; 
}

.noSound {
  width: 6px;
  height: 60px;
  transform-origin: bottom left;
  transform: rotate(45deg) scaleY(1);
}